.warning {
	border-left: 5px solid #e66465;
	margin: 10px 0;
	padding: 10px;
	background-color: #ffe7e8;
}

.warning li {
	margin: 10px;
}

.keyword-label {
	padding: 3px 5px;
	color: #fff;
	background-color: #495057;
	border-radius: 3px;
	font-family: 'Courier New', Courier, monospace;
}
/*
 * Picker button container
 */
.data-source {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 650px;
  height: 200px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 50px;
}

/*
 * Data picker buttons
 */
.data-source .data-picker {
  font-size: 1.25em;
  font-weight: 700;
  color: white;

  height: 200px;
  width: 300px;
  border-radius: 4px;

  text-align: center;
  line-height: 200px;

  background-color: #fc913a;
  display: inline-block;
  cursor: pointer;
}

.data-source .data-picker:hover {
	background-color: #ff4e50;
}

.data-source .data-picker input {
	display: none;
}

.reg-group ol {
	margin: 0;
	padding: 0;
	list-style-type: none;
	color: #fff;
}

.reg-group a {
	text-decoration: none;
}

.reg-group a:visited {
	color: #fff;
}

.reg-group a:hover {
	background-color: #369;
}

.group-title {
	color: #FFF;
	background-color: #ff6f69;
	padding: 5px;
	border-radius: 3px;
 	cursor: pointer;
 	margin: 10px auto;

	display: grid;
	grid-template-columns: auto 150px;
}

.reg-title {
	color: #FFF;
	background-color: #61c090;
	margin: 10px 0px 0px 10px;
	padding: 5px;
	border-radius: 3px;
 	cursor: pointer;

	display: grid;
	grid-template-columns: 150px auto 100px 50px;
}



.reg-group li > a, .reg-group li > ol {
	margin-left: 10px;
}
.reg-container {
	display: grid;
	grid-template-columns: calc(2px * 2 + 30px * 32 + 1px * 31) 90px;
	width: 1085px;	
	margin: auto;
	
	line-height: 30px;
	text-align: center;

	font-size: small;

	-webkit-user-select: none;

	   -moz-user-select: none;

	    -ms-user-select: none;

	        user-select: none;
}

.bit-pos-marker {
	grid-area: 1 / 1 ;
	border: 2px solid white;

	display: grid;
	grid-template-columns: repeat(32, 30px);
	grid-auto-rows: 30px;
	grid-gap: 1px;

	font-style: italic;
}

.byte-offset {
	grid-area: 2 / 2;

	display: grid;
	grid-template-columns: 1fr;
	grid-auto-rows: 30px;
	grid-gap: 1px;

	line-height: 30px;

	border: 2px solid white;
}

.field-container {
	grid-area: 2 / 1;

	display: grid;
	grid-template-columns: repeat(32, 30px);
	grid-auto-rows: 30px;
	grid-gap: 1px;
	background-color: #999999;
	
	border: 2px solid #428bca;
	font-size: 7pt;
}

.field-fragment {
	cursor: pointer;
	background-color: #dfe3ee;
}

.field-fragment-unused {
	background-color: gray;
}


table.field-table {
	width: 800px;
	margin: 50px auto;
	overflow: hidden;
	font-size: 90%;
	border: 1px solid #ccc;
	border-collapse: collapse;
	text-align: left;
}

table.field-table th {
	border: 1px solid #ccc;
	border-collapse: collapse;
	text-align: left;
	padding: 5px;
	background: #00cccc;
	color: #fff;
	position: relative;
}

table.field-table td {
  border: 1px solid #ccc;
  border-collapse: collapse;
  text-align: left;
  padding: 5px;
  position: relative;
}

table.field-table th.focus-header {
	background-color: orchid;
}

table.field-table th.value-header {
	cursor: pointer;
	background-color: cadetblue
}

table.field-table td.highlight-field {
	background-color: orchid;
	color: #fff;
}

table.field-table tr:hover {
	background-color: #ffa;
}
  
table.field-table td:hover::after,
table.field-table th:hover::after {
	content: "";
	position: absolute;
	background-color: #ffa;
	left: 0;
	top: -5000px;
	height: 10000px;
	width: 100%;
	z-index: -1;
}

.reg-decode textarea {
	width: 600px;
	height: 120px;
	padding: 5px;
}

div.symbolic-values {
	padding: 5px 0 0 5px;
	display: grid;
	grid-template-columns: auto auto auto auto 1fr;
	grid-column-gap: 5px;
}
.group-editor {
	margin-top: 20px;
	display: grid;
	grid-gap: 10px;
	grid-template-columns: 80px 200px;
}
.reg-editor {
	margin: 10px auto;
	display: grid;
	grid-gap: 10px;
	grid-template-columns: 70px 200px 50px 300px;
	grid-template-rows: auto;
	grid-template-areas: 
		"reg-name-label    reg-name-input    reg-group-label   reg-group-input"
		"reg-offset-label  reg-offset-input  reg-size-label    reg-size-input"
		"reg-summary-label reg-summary-input reg-summary-input reg-summary-input"
		"reg-detail-label  reg-detail-area   reg-detail-area   reg-detail-area"
		"field-editor      field-editor      field-editor      field-editor";
}

.reg-editor [name="name-label"] {
	grid-area: reg-name-label;
}

.reg-editor [name="name"] {
	grid-area: reg-name-input;
}

.reg-editor [name="parent-label"] {
	grid-area: reg-group-label;
}

.reg-editor [name="parent"] {
	grid-area: reg-group-input;
}

.reg-editor [name="desc-short-label"] {
	grid-area: reg-summary-label;
}

.reg-editor [name="desc_short"] {
	grid-area: reg-summary-input;
}

.reg-editor [name="desc-long-label"] {
	grid-area: reg-detail-label;
}

.reg-editor [name="desc_long"] {
	grid-area: reg-detail-area;
	height: 100px;
}

.field-editor {
	grid-area: field-editor;
}

.field-form {
	margin: 10px;
	display: grid;
	grid-gap: 10px 10px;
	grid-template-columns: 140px auto;
}

.field-form textarea {
	height: 100px;
}

.field-form .field-btns {
	grid-column: 1/3;
	width: 100%;
	display: grid;
	grid-gap: 10px;
	grid-template-columns: 1fr repeat(3, 120px) 1fr;
}

.field-form button[name="field-submit-btn"] {
	grid-column: 2;
}

.field-form button[name="field-cancel-btn"] {
	grid-column: 3;
}

.field-form button[name="field-delete-btn"] {
	grid-column: 4;
}

div.bit {
	background-color: #fff;
	cursor: pointer;
}

div.bit.active-adding {
	background-color: #deff97;
}

div.bit.active-updating {
	background-color: #ffb3ba;
}

table.symbolic-names {
	table-layout: fixed;
	border-collapse: collapse;
	/*border: 3px solid purple;*/
}

table.symbolic-names th, table.symbolic-names td {
	padding: 5px;
	text-align: left;
}
.nav-container {
	margin-top: 10px;
	padding: 5px;
	background-color: #eee;
	display: grid;
	grid-gap: 5px;
	grid-template-columns: 40px auto 60px;
	color: #4CAF50;
	
	position: -webkit-sticky;
	position: sticky;
	top: 0px;
	z-index: 999;
}

.nav-container a {
	text-decoration: none;
}

.nav-container a:visited {
	color: #4CAF50;
}

.home-btn  {
	font-size: 30px;
	grid-column: 1;
	justify-self: center;
	align-self: center;
}

.nav-breadcrumb {
	margin: 0;
	padding: 0;
	list-style: none;
	grid-column: 2;
	justify-self: start;
	align-self: center;
}

.nav-breadcrumb li {
	display: inline;
	font-size: 18px;
}

.nav-breadcrumb li:before {
	padding: 5px;
	color: black;
	content: "/\A0";
}

.nav-breadcrumb li a {
	color: #0275d8;
	text-decoration: none;
}

.nav-breadcrumb li a:hover {
	text-decoration: underline;
}

.dropdown {
	grid-column: 3;
	justify-self: center;
	align-self: center;
	position: relative;
	width: 60px;
}

.dropdown-btn {
	background-color: #4CAF50;
	color: white;
	padding: 6px;
	width: 100%;
	height: 100%;
	font-size: 16px;
	border: none;
	cursor: pointer;
}

.dropdown-content {
	display: none;
	position: absolute;
	color: black;
	background-color: #f9f9f9;
	min-width: 240px;
	box-shadow: 0px 4px 8px 0px rgba(0,0,0,0.2);
	z-index: 1;
	right: 0;
}

.dropdown-content a, .dropdown-content span {
	color: black;
	padding: 10px 14px;
	text-decoration: none;
	display: block;
}

.dropdown-content a:hover {
	background-color: #f1f1f1;
}


.dropdown-content span:hover {
	background-color: #f1f1f1;
	cursor: pointer;
}

.dropdown:hover .dropdown-content {
	display: block;
}

.dropdown:hover .dropdown-btn {
	background-color: #3e8e41;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


