/*
 * Message Banner
 */
div.message-banner {
  display: grid;
  grid-template-columns: 1fr 25px;
  grid-auto-rows: 25px;
  line-height: 25px;
  padding-left: 10px;
  border-radius: 3px;
  margin: 10px 0px;
  font-size: small;
  color: white;
}

div.message-banner-info {
  background-color: #00aedb;
}

div.message-banner-warn {
  background-color: #f37735;
}

/*
 * Starting Page
 */

div#dataSource {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 650px;
  height: 200px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 50px;
}

div#dataSource input {
  display: none;
}

div#dataSource label {
  font-size: 1.25em;
  font-weight: 700;
  color: white;
  background-color: #fc913a;
  display: inline-block;
  cursor: pointer;

  height: 200px;
  width: 300px;
  border-radius: 4px;
  
  text-align: center;
  line-height: 200px;
}

div#dataSource label:hover {
   background-color: #ff4e50;
}

div#dataSource p {
  font-size: 1.25em;
  font-weight: 700;
  color: white;
  background-color: #fc913a;
  display: inline-block;
  cursor: pointer;

  height: 200px;
  width: 300px;
  border-radius: 4px;

  text-align: center;
  line-height: 200px;

  margin: 0;
}

div#dataSource p:hover {
   background-color: #ff4e50;
}

/*
 * Content Page
 */

div#navigator {
  width: 1200px;
  margin: 10px auto;
}

div.block {
  margin: 20px auto;
}

div.block-header {
  display: grid;
  grid-template-columns: 30px 100px 1fr 40px;
  grid-auto-rows: 40px;
  background-color: #88d8b0;
  line-height: 40px;
  border-radius: 3px;
  margin: 10px 0;
}

div.block-header:hover {
  cursor: pointer;
}

div.block-header div.desc {
  display: grid;
}

div.block-header div.add {
  background-color: #ff6f69;
  display: none;
  text-align: center;
}

div.block-header div.del {
  background-color: #ffcc5c;
  display: none;
  text-align: center;
}

div.block-header:hover div.add {
  display: grid;
}

div.block-header:hover div.del {
  display: grid;
}

/*
 * Register
 */
div.register {
  margin: 10px 0 10px 20px;
}

div.register-header {
  display: grid;
  grid-template-columns: 30px 100px 100px 1fr;
  grid-auto-rows: 40px;
  background-color: #f37735;
  border-radius: 3px;

  line-height: 40px;
  cursor: pointer;
}

.register-name {
  font-weight: bold;
}

.register-desc-short {
  padding-left: 10px;
  font-style: italic;
  font-size: small;
}

div.register-detail {
  margin: 0 20px 0 20px;
}
